import { Injectable } from '@angular/core';
import { Conquista } from '../models/conquista';
import * as firebase from 'firebase';
import { Paciente } from '../models/paciente';

@Injectable({
  providedIn: 'root'
})
/**
 * Controla o acesso a conquista dos pacientes
 */
export class ConquistaService {

  private pacienteDB:firebase.firestore.CollectionReference;

  /** Lista de consquitas alcançadas */
  private readonly CONQUISTAS: Conquista[] = [
    //Total de Dias sem fumar
    new Conquista('sf1', '1 dia sem fumar', 'Parabéns! Você conseguiu ficar seu primeiro dia sem fumar', 'no-smoking.png'),
    new Conquista('sf2', '10 dia sem fumar', 'Você alcançou 10 dias sem fumar', 'no-smoking.png'),
    new Conquista('sf3', '15 dia sem fumar', 'Você alcançou 15 dias sem fumar', 'no-smoking.png'),
    new Conquista('sf4', '30 dia sem fumar', 'Você alcançou 30 dias sem fumar', 'no-smoking.png'),
    new Conquista('sf5', '50 dia sem fumar', 'Você alcançou 50 dias sem fumar', 'no-smoking.png'),
    new Conquista('sf6', '100 dia sem fumar', 'Você alcançou 100 dias sem fumar', 'no-smoking.png'),
    
    //Dias seguidos sem fumar   
    new Conquista('ssf1', '3 dias seguidos', 'Você conseguiu ficar 3 dias seguidos sem fumar!', 'no-smoking.png'),
    new Conquista('ssf2', '5 dias sem fumar', 'Uau! Você alcançou 5 dias seguidos sem fumar', 'no-smoking.png'),
    new Conquista('ssf3', 'uma semana sem fumar', 'Você conseguiu ficar uma semana sem fumar!', 'no-smoking.png'),
    new Conquista('ssf4', 'duas semanas sem fumar', 'Sua segunda semana sem fumar!', 'no-smoking.png'),
    new Conquista('ssf5', 'Completou um mês!', 'Você conseguiu ficar uma semana sem fumar!', 'no-smoking.png'),
    new Conquista('ssf6', '2 meses sem fumar!', 'Parabéns já são 2 meses sem fumar!', 'no-smoking.png'),
    new Conquista('ssf7', '3 meses sem fumar!!', 'Chegou a 3 meses sem fumar', 'no-smoking.png'),
    new Conquista('ssf8', '6 meses sem fumar!!', 'Conseguiu completar 6 meses sem fumar!', 'no-smoking.png'),
    
    //Exercicio
    new Conquista('exe1', 'Exercitou', 'Você realizou seu primeiro exercício!', 'exercise.png'),
    new Conquista('exe2', '5 dias', 'Você completou 5 dias de exercício, continue assim', 'exercise.png'),
    new Conquista('exe3', '15 dias de exercício', '15 dias práticando!!', 'exercise.png'),
    new Conquista('exe4', '30 dias', 'Mantenha seu ritmo!', 'exercise.png'),
    new Conquista('exe5', '60 dias', 'Você alcançou uma ótima marca', 'exercise.png'),
    new Conquista('exe6', '90 dias', 'Parabéns! Mas não pare por aqui!', 'exercise.png'),
    
    //Financeiro
    new Conquista('fin1', 'Economizando', 'Você começou a economizar seus primeiros reias', 'money.png'),
    new Conquista('fin2', '50 reais!', 'Você já conseguiu juntar 50 reais!', 'money.png'),
    new Conquista('fin3', '100 reais!', 'Seus primeiros 100 reais!', 'money.png'),
    new Conquista('fin4', '250 reais!', 'Sabe aquele presente que você queria? Voce já pode se dar', 'money.png'),
    new Conquista('fin5', '500 reais!', 'Já é possível realizar uma pequena viagem!', 'money.png'),
    new Conquista('fin6', '1000 reais!', 'Nossa R$ 1000, você já pode fazer muita coisa', 'money.png'),
  ];

  constructor() { 
    this.pacienteDB = firebase.firestore().collection('pacientes');
  }

  /**
   * Lista as conquistas ja alcançadas pelo usuário
   * @param paciente 
   */
  public async buscarConsquistasAlcancadas(paciente: Paciente): Promise<Conquista[]> {
    const conquistasID = await this.getConquistas(paciente.uid);

    //Recupera as alcançadas  
    const alcançadas: Conquista[] = [];
    this.CONQUISTAS.forEach(c => { if (conquistasID.includes(c.codigo)) alcançadas.push(c); })

    return alcançadas;
  }
  
  /**
   * Lista as conquistas ja alcançadas pelo usuário
   * @param paciente 
   */
  public async buscarConsquistasPendentes(paciente: Paciente): Promise<Conquista[]> {
    const conquistasID = await this.getConquistas(paciente.uid);

    //Recupera as pendentes
    const pendentes: Conquista[] = [];
    this.CONQUISTAS.forEach(c => { if (!conquistasID.includes(c.codigo)) pendentes.push(c); })

    return pendentes;
  }
  
  /**
   * Retorna o código das conquistas já alcançadas pelo usuário
   * @param pacienteID 
   */
  private async getConquistas(pacienteID: string): Promise<string[]> {
    const snapConquistas = await this.pacienteDB.doc(pacienteID).collection('conquistas').get();
    const conquistasID: string[] = []
    
    snapConquistas.forEach(doc => { conquistasID.push(doc.id) })

    return conquistasID;
    
  }
  
  /**
   * Recebe o paciente para liberar as conquistas
   * @param paciente 
   */
  public liberarConquistas(paciente: Paciente) {
    let salvar = [];
    salvar = salvar.concat(this.conquistaDiasSemFumar(paciente));
    salvar = salvar.concat(this.conquistaDiasSeguidosSemFumar(paciente));
    salvar = salvar.concat(this.conquistaExercicio(paciente));
    salvar = salvar.concat(this.conquistaFinanceira(paciente));
    salvar.forEach(conquistaID => {
      this.pacienteDB.doc(paciente.uid).collection('conquistas').doc(conquistaID).set({conquistado:true})
    })
  }

  /**
   * Libera as conquistas de sias sem fumar (mesmo não seguidos)
   * @param paciente 
   */
  private conquistaDiasSemFumar(paciente: Paciente) {
    const salvar = [];
    if (paciente.diasSemFumar >= 1) salvar.push('sf1');
    if (paciente.diasSemFumar >= 10) salvar.push('sf2');
    if (paciente.diasSemFumar >= 15) salvar.push('sf3');
    if (paciente.diasSemFumar >= 30) salvar.push('sf4');
    if (paciente.diasSemFumar >= 50) salvar.push('sf5');
    if (paciente.diasSemFumar >= 100) salvar.push('sf6');
    return salvar;
  }

  /**
   * Libera Conquistas de Dias Seguidos Sem Fumar
   * @param paciente 
   */
  private conquistaDiasSeguidosSemFumar(paciente: Paciente) {
    const salvar = [];
    if (paciente.diasSeguidosSemFumar >= 3) salvar.push('ssf1');
    if (paciente.diasSeguidosSemFumar >= 5) salvar.push('ssf2');
    if (paciente.diasSeguidosSemFumar >= 7) salvar.push('ssf3');
    if (paciente.diasSeguidosSemFumar >= 14) salvar.push('ssf4');
    if (paciente.diasSeguidosSemFumar >= 30) salvar.push('ssf5');
    if (paciente.diasSeguidosSemFumar >= 60) salvar.push('ssf6');
    if (paciente.diasSeguidosSemFumar >= 90) salvar.push('ssf7');
    if (paciente.diasSeguidosSemFumar >= 183) salvar.push('ssf8');
    return salvar;
  }

  /**
   * Libera Conquistas de Dias Seguidos Sem Fumar
   * @param paciente 
   */
  private conquistaExercicio(paciente: Paciente) {
    const salvar = [];
    if (paciente.diasPraticoExercicio >= 1) salvar.push('exe1');
    if (paciente.diasPraticoExercicio >= 5) salvar.push('exe2');
    if (paciente.diasPraticoExercicio >= 15) salvar.push('exe3');
    if (paciente.diasPraticoExercicio >= 30) salvar.push('exe4');
    if (paciente.diasPraticoExercicio >= 60) salvar.push('exe5');
    if (paciente.diasPraticoExercicio >= 90) salvar.push('exe6');
    return salvar;
  }

  /**
   * Libera Conquistas Financeiras
   * @param paciente 
   */
  private conquistaFinanceira(paciente: Paciente) {
    const salvar = [];
    if (paciente.dinheiroAcumulado >= 1) salvar.push('fin1');
    if (paciente.dinheiroAcumulado >= 50) salvar.push('fin2');
    if (paciente.dinheiroAcumulado >= 100) salvar.push('fin3');
    if (paciente.dinheiroAcumulado >= 250) salvar.push('fin4');
    if (paciente.dinheiroAcumulado >= 500) salvar.push('fin5');
    if (paciente.dinheiroAcumulado >= 1000) salvar.push('fin6');
    return salvar;
  }
}
